import Vue from 'vue';
import Vuex from 'vuex';
import createPersistedState from 'vuex-persistedstate';
import axios from 'axios'

Vue.use(Vuex);

export default new Vuex.Store({
  state: {
    theme: 'light', // 默认主题为 light
    backtext: '',
    user: {},
    posts: [],
    artview: {},
    aceryb: {},
    symbolsItem: {}
  },
  mutations: {
    setTheme(state, newTheme) {
      state.theme = newTheme;
      document.getElementById('app').setAttribute('data-theme', newTheme);
    },
    setbacktext(state, newBacktext) {
      state.backtext = newBacktext
    },
    setUser(state, newUser) { // 用户用资料
      state.user = newUser
    },
    setPosts(state, newPosts) { //用户文章
      state.posts = newPosts
    },
    setView(state, newartView) { //用户单个文章
      state.artview = newartView
    },
    setAceryb(state, newAceryb) { //音标
      state.aceryb = newAceryb
    },
    setSymbolsItem(state, newSymbolsItem) { //音标
      state.symbolsItem = newSymbolsItem
    }
  },
  actions: {
    async fetchIpaData({
      commit
    }) {
      try {
        if (!symbolsItem) {
          const response = await axios.get('/api');
          console.log(response.data, 'response.data'); 
          commit('setAceryb', response.data); // 更新状态 
          commit('setSymbolsItem', response.data.vowels.symbols); // 更新状态 
        }

      } catch (error) {
        // commit('setError', error); // 设置错误信息 
      }
    }
  },
  getters: {
    aceryb: state => state.aceryb,
    symbolsItem: state => state.symbolsItem,

  },
  plugins: [
    createPersistedState({
      storage: window.localStorage, // 使用 localStorage 来持久化主题状态
      paths: ['theme', 'backtext', 'user', 'posts', 'artview', 'aceryb', 'symbolsItem'] // 持久化数据状态
    })
  ]
});