// src/router/index.js
import Vue from 'vue';
import Router from 'vue-router';
import store from "@/stores/theme";

Vue.use(Router);

const router = new Router({
  mode: 'history',
  routes: [{
      path: '/',
      name: 'Home',
      component: () => import('@/components/home/Home.vue'), // 首页
      meta: {
        activeBtn: 1
      },
    },
    {
      path: '/phonetic',
      name: 'Phonetic',
      component: () => import('@/components/phonetic/Phonetic.vue'), // 音标
      meta: {
        activeBtn: 2
      },
      beforeEnter: (to, from, next) => {
         // 在路由进入前请求数据并存储到 Vuex
        store.dispatch('fetchIpaData')
          .then(d => { 
            next(); // 允许路由进入
          })
          .catch(error => {
            console.error('请求数据失败:', error);
            next(false); // 如果请求失败，取消路由跳转
          });
      }
    },
    {
      path: '/individual',
      name: 'Individual',
      component: () => import('@/components/center/Individual.vue'), // 个人中心组件
      meta: {
        activeBtn: 4
      },
    },
    {
      path: '/edit-profile',
      name: 'EditProfile',
      component: () => import('@/components/center/EditProfile.vue'), // 编辑个人资料页面
    },
    {
      path: '/update-Password',
      name: 'UpdatePassword',
      component: () => import('@/components/center/Password.vue'), // 修改密码页面
    },
    {
      path: '/artic-leView',
      name: 'ArticleView',
      component: () => import('@/components/center/ArticleView.vue'), // 文章页面
    },
    {
      path: '/Add-article',
      name: 'AddArticle',
      component: () => import('@/components/center/AddArticle.vue'), // 发布文章页面
    },
  ],
  scrollBehavior(to, from, savedPosition) {
    if (savedPosition) {
      return savedPosition; // 返回上次滚动位置
    } else {
      return {
        x: 0,
        y: 0
      }; // 默认滚动到顶部
    }
  },
});

// 在路由守卫中设置 activeBtn
router.beforeEach((to, from, next) => {
  // 通过 meta 获取 activeBtn，设置状态
  if (to.meta && to.meta.activeBtn !== undefined) {
    // 如果你使用 Vuex 来管理状态
    // store.dispatch('updateActiveBtn', to.meta.activeBtn);

    // 或者直接操作组件的状态
    // 例如，通过全局事件总线来通知组件更新状态
    // app.$emit('setActiveBtn', to.meta.activeBtn);

    // 或者直接在导航栏组件中通过 this.$route.meta 来访问
    // 这里就已经传递了 activeBtn 状态
    // console.log('Active Button Index:', to.meta.activeBtn);
  }
  next();
});


export default router;